"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import {
  Clock,
  FileText,
  Heart,
  Loader2,
  CheckCircle2,
  AlertCircle,
  Stethoscope,
} from "lucide-react";
import {
  getPendingConsultasRequest,
  approveConsultaRequest,
  updateConsultaRequest,
} from "@/api/requests/consultas";
import { getDoencaRequest } from "@/api/requests/doenca";
import { getSintomaRequest } from "@/api/requests/sintomas";
import { Checkbox } from "@/components/ui/checkbox";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { UserDropdown } from "./user-dropdown";
import { Skeleton } from "@/components/ui/skeleton";

interface Diagnostico {
  _id: string;
  doenca: string;
  porcentagem: number;
  sintomasComuns: string[];
}

interface PreDiagnosis {
  id: string;
  patientName: string;
  patientBI: string;
  symptoms: string[];
  symptomIds?: string[];
  descricao?: string;
  submittedDate: string;
  status: string;
  diagnosticos: Diagnostico[];
}

interface Recomendacao {
  _id: string;
  nome: string;
  descricao?: string;
  origem?: "sintoma" | "doenca";
}

export function DoctorPreDiagnosisValidation(): JSX.Element {
  const router = useRouter();

  const [pendingPreDiagnoses, setPendingPreDiagnoses] = useState<PreDiagnosis[]>([]);
  const [selectedReport, setSelectedReport] = useState<PreDiagnosis | null>(null);
  const [selectedDoencaId, setSelectedDoencaId] = useState<string | null>(null);
  const [availableRecomendacoes, setAvailableRecomendacoes] = useState<Recomendacao[]>([]);
  const [selectedRecomendacoes, setSelectedRecomendacoes] = useState<string[]>([]);
  const [validationNotes, setValidationNotes] = useState("");
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isValidating, setIsValidating] = useState(false);
  const [loadingRecomendacoes, setLoadingRecomendacoes] = useState(false);

  useEffect(() => {
    async function fetchPending() {
      try {
        setLoading(true);
        const token = localStorage.getItem("token");
        if (!token) {
          router.push("/login");
          return;
        }
        const res = await getPendingConsultasRequest(token);
        if (!res.ok) throw new Error("Falha ao buscar consultas pendentes");
        const json = await res.json();
        setPendingPreDiagnoses(json.data || []);
        setSelectedReport(json.data?.[0] || null);
      } catch (e: any) {
        setError(e.message);
      } finally {
        setLoading(false);
      }
    }
    fetchPending();
  }, [router]);

  useEffect(() => {
    async function fetchRecs() {
      if (!selectedReport || !selectedDoencaId) return;
      setLoadingRecomendacoes(true);
      try {
        const recs: Recomendacao[] = [];
        const dRes = await getDoencaRequest(selectedDoencaId);
        if (dRes.ok) {
          const d = await dRes.json();
          d.data?.recomendacoes?.forEach((r: any) =>
            recs.push({ _id: r._id, nome: r.nome, descricao: r.descricao, origem: "doenca" })
          );
        }
        if (selectedReport.symptomIds) {
          await Promise.all(
            selectedReport.symptomIds.map(async (id) => {
              const sRes = await getSintomaRequest(id);
              if (sRes.ok) {
                const s = await sRes.json();
                s.data?.recomendacoes?.forEach((r: any) => {
                  if (!recs.find((x) => x._id === r._id)) {
                    recs.push({ _id: r._id, nome: r.nome, descricao: r.descricao, origem: "sintoma" });
                  }
                });
              }
            })
          );
        }
        setAvailableRecomendacoes(recs);
      } finally {
        setLoadingRecomendacoes(false);
      }
    }
    fetchRecs();
  }, [selectedDoencaId, selectedReport]);

  async function handleValidate() {
    if (!selectedReport || !selectedDoencaId) return;
    setIsValidating(true);
    try {
      const token = localStorage.getItem("token");
      if (!token) return;
      await approveConsultaRequest(selectedReport.id, token);
      await updateConsultaRequest(selectedReport.id, token, {
        doenca: selectedDoencaId,
        recomendacoes_medicos: selectedRecomendacoes,
        recomendacoes_livres: validationNotes ? [validationNotes] : [],
      });
      setPendingPreDiagnoses((p) => p.filter((c) => c.id !== selectedReport.id));
      setSelectedReport(null);
      alert("Consulta validada com sucesso");
    } catch (e: any) {
      alert(e.message);
    } finally {
      setIsValidating(false);
    }
  }

  return (
    <div className="p-6">
      <h1 className="text-xl font-bold flex items-center gap-2">
        <Stethoscope /> Validação de Pré‑Diagnóstico
      </h1>

      {loading && <Skeleton className="h-10 w-full mt-4" />}
      {error && <Alert variant="destructive">{error}</Alert>}

      <div className="grid grid-cols-3 gap-6 mt-6">
        <Card className="col-span-1">
          <CardHeader>
            <CardTitle>Pendentes</CardTitle>
          </CardHeader>
          <CardContent className="space-y-2">
            {pendingPreDiagnoses.map((c) => (
              <button
                key={c.id}
                className="w-full text-left p-2 border rounded"
                onClick={() => setSelectedReport(c)}
              >
                <p className="font-medium">{c.patientName}</p>
                <p className="text-xs text-muted-foreground">{c.patientBI}</p>
              </button>
            ))}
          </CardContent>
        </Card>

        <Card className="col-span-2">
          <CardHeader>
            <CardTitle>Detalhes</CardTitle>
          </CardHeader>
          <CardContent>
            {!selectedReport ? (
              <p>Selecione uma consulta</p>
            ) : (
              <Tabs defaultValue="condicoes">
                <TabsList>
                  <TabsTrigger value="condicoes">Condições</TabsTrigger>
                  <TabsTrigger value="validar">Validar</TabsTrigger>
                </TabsList>

                <TabsContent value="condicoes">
                  <RadioGroup onValueChange={setSelectedDoencaId}>
                    {selectedReport.diagnosticos.map((d) => (
                      <div key={d._id} className="flex gap-2 items-center">
                        <RadioGroupItem value={d._id} />
                        <span>{d.doenca} ({d.porcentagem}%)</span>
                      </div>
                    ))}
                  </RadioGroup>
                </TabsContent>

                <TabsContent value="validar" className="space-y-4">
                  {availableRecomendacoes.map((r) => (
                    <div key={r._id} className="flex items-center gap-2">
                      <Checkbox
                        checked={selectedRecomendacoes.includes(r._id)}
                        onCheckedChange={() =>
                          setSelectedRecomendacoes((p) =>
                            p.includes(r._id) ? p.filter((i) => i !== r._id) : [...p, r._id]
                          )
                        }
                      />
                      <span>{r.nome}</span>
                    </div>
                  ))}

                  <Textarea
                    placeholder="Observações médicas"
                    value={validationNotes}
                    onChange={(e) => setValidationNotes(e.target.value)}
                  />

                  <Button onClick={handleValidate} disabled={isValidating}>
                    {isValidating ? "Validando..." : "Validar"}
                  </Button>
                </TabsContent>
              </Tabs>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
